  main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(180deg, #EBF2FC 0%, #EEF8F9 100%);
  }
}

main.dark-theme {
  height: auto;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(180deg, #040918 0%, #091540 100%);
  color: hsl(200, 60%, 99%);
  padding: 20px;
}
main.dark-theme #head {
  width: 95%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(250.92, 252.96, 253.98, 0.8);
  border-radius: 10px;
  padding: 10px;
}
main.dark-theme #head img {
  width: 35px;
  height: 35px;
}
main.dark-theme #head p {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 25px;
  font-weight: 700;
  color: hsl(200, 60%, 99%);
}
main.dark-theme #head #switch-theme {
  width: 40px;
  height: 40px;
  background-color: hsl(226, 11%, 37%);
  padding: 5px;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
main.dark-theme #head #switch-theme #dark {
  display: none;
}
main.dark-theme #head #switch-theme #light {
  width: 25px;
  height: 25px;
  display: block;
}
main.dark-theme h1 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: hsl(200, 60%, 99%);
  margin-top: 20px;
}
main.dark-theme nav {
  width: 100%;
  height: auto;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
main.dark-theme nav ul {
  width: 95%;
  height: auto;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
main.dark-theme nav ul li {
  width: auto;
  height: auto;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid hsl(226, 11%, 37%);
  background-color: hsl(225, 23%, 24%);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
main.dark-theme nav ul li.actived {
  background-color: hsl(3, 86%, 64%);
  color: hsl(227, 75%, 14%);
  border: none;
}
main.dark-theme section#extension-list {
  width: 95%;
  height: auto;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}
main.dark-theme section#extension-list .extension-card {
  width: 100%;
  height: auto;
  border-radius: 20px;
  border: 1px solid hsl(226, 11%, 37%);
  background-color: hsl(225, 23%, 24%);
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 15px;
}
main.dark-theme section#extension-list .extension-card img {
  flex-basis: 20%;
}
main.dark-theme section#extension-list .extension-card #info {
  flex-basis: 80%;
}
main.dark-theme section#extension-list .extension-card button {
  flex-basis: 80%;
  cursor: pointer;
}
main.dark-theme section#extension-list .extension-card #switch {
  cursor: pointer;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Noto Sans JP", sans-serif;
  box-sizing: border-box;
  transition: all ease-in-out 0.5s;
